
.message-wraper[data-v-3d11049c]{
  position: fixed;
  display: flex;
  box-sizing: border-box;
  flex-direction: column-reverse;
  flex-wrap: wrap;
  justify-content: flex-end;
  text-align: left;
  bottom: 0;
  right: 0;
  width: 300px;
  margin: 0 1rem 1rem 0;
}
.popup-message[data-v-3d11049c]{
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: 300px;
  padding: 1rem 0.75rem;
  margin: 0.25rem 0;
  border: 2px solid transparent;
  border-radius: 0.5rem;
  font-size: 12px;
}

/* animation */
.message-enter-active[data-v-3d11049c]{
  transition: all 0.25s ease;
}
.message-leave-active[data-v-3d11049c] {
  /* position: absolute; */
  transition: all 0.5s ease;
}
.message-enter-from[data-v-3d11049c],
.message-leave-to[data-v-3d11049c] {
  opacity: 0;
  transform: translateX(30px);
}
/* .message-move {
  transition: transform 1s;
} */

/* colors */
.popup-message.success[data-v-3d11049c]{
  border-color: #28a745;
  background-color: #f3fcf5;
}
.popup-message.error[data-v-3d11049c]{
  border-color: #dc3545;
  background-color: #fdf1f2;
}
.popup-message.warning[data-v-3d11049c]{
  border-color: #ffc107;
  background-color: #fffcf5;
}
.popup-message.info[data-v-3d11049c]{
  border-color: #17a2b8;
  background-color: #f2fcfd;
}
.popup-close[data-v-3d11049c]{
  background: transparent;
  border: none;
  padding: 0 1rem;
  position: relative;
}
.popup-close[data-v-3d11049c]::after,
.popup-close[data-v-3d11049c]::before{
  content: "";
  display: block;
  height: 12px;
  width: 12px;
  position: absolute;
  top: -6px;
  border-top: 1px solid transparent;
  background: rgba(0, 0, 0, 0);
}
.popup-close[data-v-3d11049c]::after{
  left: 3px;
  transform: rotate(45deg);
}
.popup-close[data-v-3d11049c]::before{
  left: 11px;
  transform: rotate(-45deg);
}
.popup-message.success .popup-close[data-v-3d11049c]::after,
.popup-message.success .popup-close[data-v-3d11049c]::before{
  border-color: #28a745;
}
.popup-message.error .popup-close[data-v-3d11049c]::after,
.popup-message.error .popup-close[data-v-3d11049c]::before{
  border-color: #dc3545;
}
.popup-message.warning .popup-close[data-v-3d11049c]::after,
.popup-message.warning .popup-close[data-v-3d11049c]::before{
  border-color: #ffc107;
}
.popup-message.info .popup-close[data-v-3d11049c]::after,
.popup-message.info .popup-close[data-v-3d11049c]::before{
  border-color: #17a2b8;
}


